import React, { useState, useEffect } from 'react';
import {
    DndContext,
    closestCenter,
    useSensor,
    useSensors,
    PointerSensor,
    rectIntersection, // Importado rectIntersection
} from '@dnd-kit/core';
import {
    SortableContext,
    useSortable,
    arrayMove,
    verticalListSortingStrategy,
} from '@dnd-kit/sortable';
import './MyBooksScreen.css';

const SortableBook = ({ book, index, removeBook }) => {
    const {
        attributes,
        listeners,
        setNodeRef,
        transform,
        transition,
        isDragging, // Indica si el elemento se est arrastrando
    } = useSortable({ id: book.isbn });

    const style = {
        transform: transform ? `translate3d(${transform.x}px, ${transform.y}px, 0)` : undefined,
        transition,
    };

    const bookClass = `bk-book ${isDragging ? 'bk-book-dragging' : ''}`; // Clase adicional al arrastrar

    return (
        <li ref={setNodeRef} style={style} {...attributes}>
            <div className={bookClass}> {/* Usamos la clase dinmica */}
                <div className="bk-front" {...listeners}> {/* Drag handle aqu */}
                    <div className="bk-cover-back"></div>
                    <div
                        className="bk-cover"
                        style={{
                            backgroundImage: book.cover ? `url(${book.cover})` : 'none',
                        }}
                    ></div>
                </div>
                <div className="bk-info">
                    <h3>
                        <span>{book.author || 'Autor'}</span>
                        <span>{book.title || 'Ttulo'}</span>
                    </h3>
                    <p>{book.shortDescription || 'Sin descripcin corta.'}</p>
                    <button
                        className="bk-remove-button"
                        onClick={(e) => {
                            e.preventDefault();
                            e.stopPropagation();
                            removeBook(book.isbn);
                        }}
                    >
                        Eliminar
                    </button>
                </div>
            </div>
        </li>
    );
};

const MyBooksScreen = () => {
    const [myBooks, setMyBooks] = useState(() => {
        const storedBooks = localStorage.getItem('myBooks');
        return storedBooks ? JSON.parse(storedBooks) : [];
    });

    useEffect(() => {
        localStorage.setItem('myBooks', JSON.stringify(myBooks));
    }, [myBooks]);

    const sensors = useSensors(
        useSensor(PointerSensor, {
            activationConstraint: {
                delay: 100, // Pequeo retraso para evitar arrastres accidentales con toques rpidos
                tolerance: 5, // Ligero movimiento permitido antes de iniciar el arrastre
            },
        })
    );

    const handleDragEnd = (event) => {
        const { active, over } = event;
        if (active.id !== over?.id) {
            const oldIndex = myBooks.findIndex(book => book.isbn === active.id);
            const newIndex = myBooks.findIndex(book => book.isbn === over.id);
            setMyBooks((items) => arrayMove(items, oldIndex, newIndex));
        }
    };

    const removeBook = (isbn) => {
        setMyBooks((prev) => prev.filter(book => book.isbn !== isbn));
    };

    return (
        <div className="container">
            <header>
                <h1>Mis Libros</h1>
            </header>
            <main className="main">
                {myBooks.length === 0 ? (
                    <p>No hay libros guardados.</p>
                ) : (
                    <DndContext
                        sensors={sensors}
                        collisionDetection={rectIntersection} // Usando rectIntersection
                        onDragEnd={handleDragEnd}
                    >
                        <SortableContext
                            items={myBooks.map(book => book.isbn)}
                            strategy={verticalListSortingStrategy}
                        >
                            <ul id="bk-list">
                                {myBooks.map((book, index) => (
                                    <SortableBook
                                        key={book.isbn}
                                        book={book}
                                        index={index}
                                        removeBook={removeBook}
                                    />
                                ))}
                            </ul>
                        </SortableContext>
                    </DndContext>
                )}
            </main>
        </div>
    );
};

export default MyBooksScreen;